@import './mixins';

.ks-font-weight-bold {
  font-weight: bold;
}

.ks-text-align-right {
  text-align: right;
}

.ks-mt-30 {
  margin-top: 30px !important;
}

.ks-col {
  padding-left: 10px;
  padding-right: 10px;
  margin-left: 10px;
  margin-right: 10px;
}

.ks-href-block {
  display: block;
  text-decoration: none;
  cursor: pointer;
}

.ks-icon-lg {
  @include gen-text(--symbolSize);
  width: unset !important;
}

.ks-btn-focus-decor-none {
  outline: none !important;
  box-shadow: none !important;
}

.white-icon {
  color: white;
}

.h-50 {
  height: 50% !important;
}

.h-80 {
  height: 80% !important;
}

/* Note: If you're using an SVG icon, you should make the class target the `<svg>` element */
.white-icon svg {
  fill: white;
}

.wrapper {
  padding-left: 80px;
  padding-right: 80px;
}

.font-weight-thin {
  font-weight: 100;
}

.font-weight-500 {
  font-weight: 500;
}

.font-weight-600 {
  font-weight: 600;
}

.text-grey-dark {
  color: var(--grey-dark);
}



.w-10 {
  width: 30%;
}

.w-40 {
  width: 40%;
}

.w-45 {
  width: 45%;
}

.w-70 {
  width: 70%;
}

.w-80 {
  width: 80%;
}

.w-85 {
  width: 85%;
}

.w-90 {
  width: 90%;
}

.w-90 {
  width: 95%;
}

.card-box-shadow {
  border: none;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5);
  background: var(--white);
}

.focus-none:focus {
  outline: none !important;
  border: 0 !important;
}

.work-break-all {
  word-break: break-all;
}