@mixin gen-text($font-size) {
  font-size: var($font-size);
  line-height: var($font-size);
}

$sm-width: 576px;
$md-width: 768px;
$lg-width: 992px;
$xl-width: 1200px;
$xxl-width: 1400px;
$xxxl-width: 1543px;
$xxxxl-width: 3000px;


@mixin sm {
  @media (min-width: #{$sm-width}) {
    @content;
  }
}

@mixin md {
  @media (min-width: #{$md-width}) {
    @content;
  }
}

@mixin lg {
  @media (min-width: #{$lg-width}){
    @content;
  }
}

@mixin xl {
  @media (min-width: #{$xl-width}) {
    @content;
  }
}

@mixin xxl {
  @media (min-width: #{$xxl-width}) {
    @content;
  }
}

@mixin xxxl {
  @media (min-width: #{$xxxl-width}) {
    @content;
  }
}

@mixin xxxxl {
  @media (min-width: #{$xxxxl-width}) {
    @content;
  }
}
