@import './mixins';

$fontSizeSm: 12px;
$fontSizeMd: 16px;
$fontSizeLg: 30px;
$fontSizeXl: 45px;
$fontSizeXxl: 50px;

$symbolSizeSm: 50px;
$symbolSizeMd: 10px;
$symbolSizeLg: 150px;
$symbolSizeXl: 250px;

$videoWidthSm: 100px;
$videoWidthMd: 300px;
$videoWidthLg: 1000px;
$videoWidthXl: 1500px;

$color-light: #ECF8E6;
$color-accent:  #3A532C;
$color-green-light: #6F8F5D;
$color-secondary: #C4A474;
$color-grey-light: #D4D4D4;
$color-grey: #9E9E9E;
$color-grey-dark: #757575;

/* Customize Bootstrap Variables */
$theme-colors: (
  primary: $color-accent,
  secondary: $color-secondary,
  light: $color-light,
);

/* You can add global styles to this file, and also import other style files */
:root {
  --toolbarHeight: 41px;
  --navBottomHeight: 29px;
  --fontSize: #{$fontSizeSm};
  --symbolSize: #{$symbolSizeSm};
  --videoWidth: #{$videoWidthSm};
  --grey: #{$color-grey};
  --grey-dark: #{$color-grey-dark};
  --grey-light: #{$color-grey-light};
  --green-light: #{$color-green-light};
  --light: #{$color-light};

  @include sm {
    --toolbarHeight: 41px;
    --navBottomHeight: 29px;
  };

  @include md {
    --symbolSize: #{$symbolSizeMd};
    --videoWidth: #{$videoWidthMd};
  };

  @include lg {
    --toolbarHeight: 194.75px;
    --navBottomHeight: 69.5px;
    --fontSize: #{$fontSizeLg};
    --symbolSize: #{$symbolSizeLg};
    --videoWidth: #{$videoWidthLg}
  };

  @include xl {
    --toolbarHeight: 139px;
    --navBottomHeight: 29px;
    --fontSize: #{$fontSizeXl};
    --symbolSize: #{$symbolSizeLg};
    --videoWidth: #{$videoWidthLg};
  };

  @include xxxxl {
    --toolbarHeight: 140px;
    --navBottomHeight: 29px;
    --fontSize: #{$fontSizeXl};
    --symbolSize: #{$symbolSizeXl};
    --videoWidth: #{$videoWidthXl};
  };
}